import { BASE_URL } from '@/api'
export default {
	props: {
		showCheckbox: {type: Boolean, default: false},
		showTime: Boolean,
		item: Object,
		index: Number,
		user_comment: Array
	},
	name: '',
	data () {
		return {
			newItem: this.item
		}
	},
	computed: {
		BASE_URL () {
			return BASE_URL
		}
	},
	onLoad () {

	},
	methods: {
		onCheckboxClickEvent (item, index) {
			this.$emit('onCheckboxClickEvent', {item, index})
		}
	}
}
